import { createStore } from 'redux'
import { findComponent } from './findComponent';
import Root from './MainInitialState/MainInitialState'
import * as actions from './actions.js'
/* 
toRender -  ,       
Root -  ,         '',      
*/
let initialState = {}
initialState.toRender = Root
initialState.Root = Root

const store = createStore(rootReducer)
/* 
     
       
          
*/
/* 
  state      rootReducer
*/
function rootReducer(state = initialState, action) {
	let newToRender
	switch (action.type) {

		case actions.JUMP:
			newToRender = findComponent(state.Root, action.path)
			if (!newToRender)
				throw new Error('findComponent(...): NO SUCH COMPONENT FOUND!')
			return {
				...state,
				toRender: newToRender
			}

		case actions.TRANSFER:
			newToRender = state.toRender.children
				.find(child => child.meta.id === action.id)
			if (!newToRender) throw new Error('actions.TRANSFER: NO SUCH CHILD FOUND!')
			return {
				...state,
				toRender: newToRender
			}

		default:
			return state
	}
}
window.store = store
export default store
